.fondContent{margin-top:100px}.fondContent .container{width:100%;max-width:1200px;margin:0 auto;padding:0 20px}.fondContent .content{background-color:#fff;border-radius:12px;box-shadow:0 10px 30px rgba(0,0,0,.1);padding:40px;transition:transform .3s ease}.fondContent .content:hover{transform:translateY(-5px)}.fondContent .title{font-size:36px;color:#2c3e50;text-align:center;margin-bottom:20px}.fondContent .divider{border:none;height:2px;background-color:#e0e0e0;margin:30px 0}.fondContent .donationsList{display:grid;gap:30px}.fondContent .donationItem{background-color:#f8f9fa;border-radius:8px;padding:20px;transition:all .3s ease}.fondContent .donationItem:hover{box-shadow:0 5px 15px rgba(0,0,0,.1)}.fondContent .donor{font-size:20px;color:#34495e;margin-bottom:10px}.fondContent .amount{font-size:24px;color:#27ae60;font-weight:bold;margin-bottom:10px}.fondContent .totalPrice{margin-top:20px}.fondContent .totalPrice span{font-size:22px;color:#27ae60;font-weight:bold}.fondContent .comment{font-size:16px;color:#7f8c8d;font-style:italic}.fondContent .message{text-align:center;font-size:18px;color:#7f8c8d;margin:20px 0}.fondContent .donateLink{display:block;text-align:center;margin-top:40px}.fondContent .donateButton{background-color:#3498db;color:#fff;border:none;border-radius:30px;padding:15px 40px;font-size:18px;font-weight:bold;cursor:pointer;transition:all .3s ease;text-transform:uppercase;letter-spacing:1px}.fondContent .donateButton:hover{background-color:#2980b9;transform:translateY(-2px);box-shadow:0 5px 15px rgba(52,152,219,.4)}@media(max-width: 768px){.fondContent .content{padding:30px}.fondContent .title{font-size:28px}.fondContent .donationItem{padding:15px}.fondContent .donor{font-size:18px}.fondContent .amount{font-size:20px}.fondContent .donateButton{padding:12px 30px;font-size:16px}}